.contact_cls{
    text-align: center;
    width: 148%;
    display: flex;
    height: 600px;
    justify-content: space-around;
    align-content: center;
}
.contact_cls .input{
    padding: 1px 2px 3px 4px;
}
.central_form form {
    height: 300px;
    width: 300px;
    box-shadow: 0 0 2px 1px #fff;
    display: flex;
    color: #111;
    padding: 15px;
    justify-content: center;
    flex-direction: column;
    margin-top: -52px;
    opacity: .9;
    background-color: #fff;
    box-shadow: 0 0 20px -2px #1f2235;
    border-radius: 12px;

}
div .contact_cls{
    position: absolute;
    top: 220px;
    
    /* border: 1px solid blue; */
}
