.image_resume {
    height: 16px;
    margin: 0 30px 0 0;
    filter: invert(1);
}
.bullet-container {
    align-items: center;
    height: 100%;
    width: 100%;
    position: relative;
    /* display: flex; */
    flex-direction: column;
}
.bullet {
    display: flex;
    align-items: center;
    cursor: pointer;
    background-color: #1f2235;
    height: 40px;
    margin: 60px 0;
    padding: 0 5px;
    border-radius: 20px;
    width: 30px;
    transition: width .6s ease !important;
    /* transition: width 2s; */
    text-decoration: none;
    font-size: 30px;
}
.main_resume_div {
    width: 70%;
    height: 350px;
    margin: 100px 158px 100px 184px;
}
.active {
    color: white;
    background-color: #1f2235 ;
    width: 100%;
   
}
.active .image_resume {
    filter: invert(1);
}

.bullet_label {
    font-size: 14px;
    white-space: nowrap;
    font-family: "Poppins SemiBold";
}
.resume-bullets{
    box-shadow: 15px 0 9px -15px #1f2235;
    height: 100%;
    width: 320px;
}
.resume_card {
    display: flex;
    height: 550px;
    width: 100%;
}
.bullet-icons {
    width: 34px;
    height: 550px;
    /* z-index: -1; */
    background-color: #1f2235;
    position: absolute;
}
.main_resume{
    width: 26%;
    background-color: azure;
    height: 550px;
}
.link_csssss{
    text-decoration: none;
    font-size: 30px;
}